import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'
import actions from './actions'
import mutations from './mutations'
import write from './modules/write'

Vue.use(Vuex)
export default new Vuex.Store({
  modules: {
    actions,
    mutations,
    write
  }
})



