import Axios from 'axios'
import {actionType} from '../actionType'

const state = {
  article: {
    cover: '',
    club: -1,
    title: '',
    summary: '',
    content: ''
  },
  post: {
    part: -1,
    content: '',
    images: [],
    club: -1
  }
}
const getters = {}
const mutations = {
  [actionType.write.set_article_data](state, article = {cover, club, title, summary, content}) {
    Object.assign(state.article, article)
  },
  [actionType.write.set_post_data](state, post = {part, content, images, club}) {
    Object.assign(state.post, post)
  }
}
const actions = {}

export default {
  state,
  getters,
  actions,
  mutations
}



